import * as React from "react";
import List, {ListType} from "../components/lists/list";
import {TeamMemberThumbnailsType} from "../components/team/teammember-types";
import ContactThumbnail from "../components/contact-thumbnail/contact-thumbnail";
import SideTitle from "../typography/side-title";
const doctor = require( '../img/doctor-illustration.png'); // Tell Webpack this JS file uses this image

interface IndexPageProps {
    data: {
        site: {
            siteMetadata: {
                pages: {
                    main: {
                        title: string,
                        ingress: string,
                        lists: ListType [];
                        getInTouch: string;
                    },
                    about: {
                        teams: {
                            teammembers: TeamMemberThumbnailsType[];
                        };
                    },
                };
            };
        };
    };
}

export default class extends React.Component<IndexPageProps, {}> {
    render() {
        const main = this.props.data.site.siteMetadata.pages.main;
        const lists = main.lists;
        const thumnails = this.props.data.site.siteMetadata.pages.about.teams.teammembers;
        return (
            <div className="main-page">
                <section>
                    <SideTitle>{ main.title }</SideTitle>
                    <img src={doctor} alt="illustration of the product" className="block-l"/>
                </section>

                {lists.map((list: ListType) => {
                        return <List title={list.title} listitems={list.listitems} key={list.title}/>;
                    })}
                <section className="thumbnails">
                    <SideTitle>{ main.getInTouch} </SideTitle>
                    <div>
                        {thumnails.map((thumbnail: TeamMemberThumbnailsType) => {
                            return <ContactThumbnail key={thumbnail.img}{...thumbnail} />;
                        })}
                    </div>
                </section>
            </div>
        );
    }
}

/* tslint:disable */
export const indexQuery = graphql`query IndexQuery {
 site {
      siteMetadata {
        pages {
          main {
            title,
            ingress,
            lists {
              title,
              listitems {
                title,
                content
              }
            }
            getInTouch
          },
          about {
            teams {
                teammembers {
                  name
                  title
                  phone
                  email
                  img
                }
            }
          }
        } 
      }
    }
  }
`;



