import * as React from "react";
import "./contact-thumbnail.scss";

export interface ContactThumbnail {
    name: string;
    title: string;
    phone: string;
    email: string;
    img: string;
}

const ContactThumbnail = (thumbnail: ContactThumbnail) => {
    return (
        <div className="contact-thumbnail">
            <img src={__PATH_PREFIX__ + "/img/" + thumbnail.img} alt={`Thumbnail of ${thumbnail.name}`} />
            <h2>{thumbnail.name}</h2>
            <ul>
                <li>{thumbnail.title}</li>
                <li>{thumbnail.phone}</li>
                <li>{thumbnail.email}</li>
            </ul>
        </div>
    );
};

export default ContactThumbnail;



