import * as React from "react";
import "./list.scss";
import SideTitle from "../../typography/side-title";

interface ListItemType {
    title: string;
    content: string;
}

export interface ListType {
    title: string;
    listitems: ListItemType [];
}

const List = (list: ListType) => {
    if (!list.listitems || list.listitems.length === 0) {
        return <div>Spinner</div>;
    }
    return (
        <section className="list block-l">
            <SideTitle>{list.title}</SideTitle>
            <dl>
                { list.listitems.map((item) => {
                    return (
                        [
                            <dt>{item.title}</dt>,
                            <dd>{item.content}</dd>,
                        ]
                    );
                })
                }
            </dl>
        </section>
    );
};

export default List;



